import 'svelte/internal/disclose-version';
import * as $ from 'svelte/internal/client';
import Sidebar from "./Sidebar.svelte";
import Dashboard from "./Dashboard.svelte";
import PipelineList from "./pipelines/PipelineList.svelte";
import PipelineDetail from "./pipelines/PipelineDetail.svelte";
import OutputList from "./outputs/OutputList.svelte";
import OutputDetail from "./outputs/OutputDetail.svelte";
import RunningExecutions from "./execution/RunningExecutions.svelte";
import ExecutionDetail from "./execution/ExecutionDetail.svelte";

var root_1 = $.from_html(
	`
      <!>
    `,
	1
);

var root_3 = $.from_html(
	`
      <!>
    `,
	1
);

var root_5 = $.from_html(
	`
      <!>
    `,
	1
);

var root_7 = $.from_html(
	`
      <!>
    `,
	1
);

var root_9 = $.from_html(
	`
      <!>
    `,
	1
);

var root_11 = $.from_html(
	`
      <!>
    `,
	1
);

var root_13 = $.from_html(
	`
      <!>
    `,
	1
);

var root = $.from_html(
	`

<div class="flex min-h-screen">
  <!>

  <main class="flex-1 ml-64 p-8">
    <!>
  </main>
</div>`,
	1
);

function App($$anchor, $$props) {
	$.push($$props, true);

	let currentPath = $.state($.proxy(window.location.pathname));

	function navigate(path) {
		window.history.pushState({}, "", path);
		$.set(currentPath, path, true);
	}

	$.user_effect(() => {
		const handlePopState = () => {
			$.set(currentPath, window.location.pathname, true);
		};

		window.addEventListener("popstate", handlePopState);

		return () => window.removeEventListener("popstate", handlePopState);
	});

	function getView() {
		if ($.get(currentPath) === "/" || $.get(currentPath) === "") return "dashboard";
		if ($.get(currentPath) === "/pipelines") return "pipelines";
		if ($.get(currentPath).startsWith("/pipelines/")) return "pipeline-detail";
		if ($.get(currentPath) === "/running") return "running";
		if ($.get(currentPath).startsWith("/executions/")) return "execution-detail";
		if ($.get(currentPath) === "/outputs") return "outputs";
		if ($.get(currentPath).startsWith("/outputs/")) return "output-detail";

		return "dashboard";
	}

	function getPipelineId() {
		return $.get(currentPath).replace("/pipelines/", "");
	}

	function getOutputFilename() {
		return $.get(currentPath).replace("/outputs/", "");
	}

	function getExecutionId() {
		return $.get(currentPath).replace("/executions/", "");
	}

	let view = $.derived(getView);
	let pipelineId = $.derived(getPipelineId);
	let outputFilename = $.derived(getOutputFilename);
	let executionId = $.derived(getExecutionId);

	$.next();

	var fragment = root();
	var div = $.sibling($.first_child(fragment));
	var node = $.sibling($.child(div));

	Sidebar(node, {
		get currentPath() {
			return $.get(currentPath);
		},

		navigate
	});

	var main = $.sibling(node, 2);
	var node_1 = $.sibling($.child(main));

	{
		var consequent = ($$anchor) => {
			var fragment_1 = root_1();
			var node_2 = $.sibling($.first_child(fragment_1));

			Dashboard(node_2, { navigate });
			$.next();
			$.append($$anchor, fragment_1);
		};

		var alternate_5 = ($$anchor) => {
			var fragment_2 = $.comment();
			var node_3 = $.first_child(fragment_2);

			{
				var consequent_1 = ($$anchor) => {
					var fragment_3 = root_3();
					var node_4 = $.sibling($.first_child(fragment_3));

					PipelineList(node_4, { navigate });
					$.next();
					$.append($$anchor, fragment_3);
				};

				var alternate_4 = ($$anchor) => {
					var fragment_4 = $.comment();
					var node_5 = $.first_child(fragment_4);

					{
						var consequent_2 = ($$anchor) => {
							var fragment_5 = root_5();
							var node_6 = $.sibling($.first_child(fragment_5));

							PipelineDetail(node_6, {
								get id() {
									return $.get(pipelineId);
								},

								navigate
							});

							$.next();
							$.append($$anchor, fragment_5);
						};

						var alternate_3 = ($$anchor) => {
							var fragment_6 = $.comment();
							var node_7 = $.first_child(fragment_6);

							{
								var consequent_3 = ($$anchor) => {
									var fragment_7 = root_7();
									var node_8 = $.sibling($.first_child(fragment_7));

									RunningExecutions(node_8, { navigate });
									$.next();
									$.append($$anchor, fragment_7);
								};

								var alternate_2 = ($$anchor) => {
									var fragment_8 = $.comment();
									var node_9 = $.first_child(fragment_8);

									{
										var consequent_4 = ($$anchor) => {
											var fragment_9 = root_9();
											var node_10 = $.sibling($.first_child(fragment_9));

											ExecutionDetail(node_10, {
												get executionId() {
													return $.get(executionId);
												},

												navigate
											});

											$.next();
											$.append($$anchor, fragment_9);
										};

										var alternate_1 = ($$anchor) => {
											var fragment_10 = $.comment();
											var node_11 = $.first_child(fragment_10);

											{
												var consequent_5 = ($$anchor) => {
													var fragment_11 = root_11();
													var node_12 = $.sibling($.first_child(fragment_11));

													OutputList(node_12, { navigate });
													$.next();
													$.append($$anchor, fragment_11);
												};

												var alternate = ($$anchor) => {
													var fragment_12 = $.comment();
													var node_13 = $.first_child(fragment_12);

													{
														var consequent_6 = ($$anchor) => {
															var fragment_13 = root_13();
															var node_14 = $.sibling($.first_child(fragment_13));

															OutputDetail(node_14, {
																get filename() {
																	return $.get(outputFilename);
																},

																navigate
															});

															$.next();
															$.append($$anchor, fragment_13);
														};

														$.if(
															node_13,
															($$render) => {
																if ($.get(view) === "output-detail") $$render(consequent_6);
															},
															true
														);
													}

													$.append($$anchor, fragment_12);
												};

												$.if(
													node_11,
													($$render) => {
														if ($.get(view) === "outputs") $$render(consequent_5); else $$render(alternate, false);
													},
													true
												);
											}

											$.append($$anchor, fragment_10);
										};

										$.if(
											node_9,
											($$render) => {
												if ($.get(view) === "execution-detail") $$render(consequent_4); else $$render(alternate_1, false);
											},
											true
										);
									}

									$.append($$anchor, fragment_8);
								};

								$.if(
									node_7,
									($$render) => {
										if ($.get(view) === "running") $$render(consequent_3); else $$render(alternate_2, false);
									},
									true
								);
							}

							$.append($$anchor, fragment_6);
						};

						$.if(
							node_5,
							($$render) => {
								if ($.get(view) === "pipeline-detail") $$render(consequent_2); else $$render(alternate_3, false);
							},
							true
						);
					}

					$.append($$anchor, fragment_4);
				};

				$.if(
					node_3,
					($$render) => {
						if ($.get(view) === "pipelines") $$render(consequent_1); else $$render(alternate_4, false);
					},
					true
				);
			}

			$.append($$anchor, fragment_2);
		};

		$.if(node_1, ($$render) => {
			if ($.get(view) === "dashboard") $$render(consequent); else $$render(alternate_5, false);
		});
	}

	$.next();
	$.reset(main);
	$.next();
	$.reset(div);
	$.append($$anchor, fragment);
	$.pop();
}

if (import.meta.hot) {
	App = $.hmr(App, () => App[$.HMR].source);

	import.meta.hot.accept((module) => {
		module.default[$.HMR].source = App[$.HMR].source;
		$.set(App[$.HMR].source, module.default[$.HMR].original);
	});
}

export default App;