import 'svelte/internal/disclose-version';
import * as $ from 'svelte/internal/client';

var root_2 = $.from_html(
	`
          <span class="ml-auto bg-blue-500 text-white text-xs px-2 py-0.5 rounded-full min-w-[1.25rem] text-center"> </span>
        `,
	1
);

var root_1 = $.from_html(
	`
      <a>
        <span class="text-lg"> </span>
        <span class="flex-1"> </span>
        <!>
      </a>
    `,
	1
);

var root = $.from_html(
	`

<aside class="fixed left-0 top-0 h-full w-60 bg-slate-900 text-white flex flex-col">
  <div class="p-4 border-b border-slate-700">
    <h1 class="text-xl font-bold flex items-center gap-2">
      <span class="text-2xl">âš¡</span>
      Pipeline Viewer
    </h1>
    <p class="text-xs text-slate-400 mt-1">ezAiPipeline Dashboard</p>
  </div>

  <nav class="flex-1 py-4">
    <!>
  </nav>

  <div class="p-4 border-t border-slate-700 text-xs text-slate-500">
    <p>Built with Bun + Svelte</p>
  </div>
</aside>`,
	1
);

function Sidebar($$anchor, $$props) {
	$.push($$props, true);

	let runningCount = $.state(0);

	const navItems = [
		{ path: "/", label: "Dashboard", icon: "ðŸ“Š" },
		{ path: "/pipelines", label: "Pipelines", icon: "ðŸ”§" },

		{
			path: "/running",
			label: "Running",
			icon: "â–¶ï¸",
			showCount: true
		},

		{ path: "/outputs", label: "Outputs", icon: "ðŸ“„" }
	];

	function isActive(path) {
		if (path === "/") return $$props.currentPath === "/" || $$props.currentPath === "";

		return $$props.currentPath.startsWith(path);
	}

	function handleClick(e, path) {
		e.preventDefault();
		$$props.navigate(path);
	}

	async function fetchRunningCount() {
		try {
			const res = await fetch('/api/executions/running');

			if (res.ok) {
				const data = await res.json();

				$.set(runningCount, data.count, true);
			}
		} catch {
			// Silently fail - count will remain at previous value
		}
	}

	$.user_effect(() => {
		fetchRunningCount();

		const interval = setInterval(fetchRunningCount, 2000);

		return () => clearInterval(interval);
	});

	$.next();

	var fragment = root();
	var aside = $.sibling($.first_child(fragment));
	var nav = $.sibling($.child(aside), 3);
	var node = $.sibling($.child(nav));

	$.each(node, 17, () => navItems, $.index, ($$anchor, item) => {
		$.next();

		var fragment_1 = root_1();
		var a = $.sibling($.first_child(fragment_1));

		a.__click = (e) => handleClick(e, $.get(item).path);

		var span = $.sibling($.child(a));
		var text = $.child(span, true);

		$.reset(span);

		var span_1 = $.sibling(span, 2);
		var text_1 = $.child(span_1, true);

		$.reset(span_1);

		var node_1 = $.sibling(span_1, 2);

		{
			var consequent = ($$anchor) => {
				var fragment_2 = root_2();
				var span_2 = $.sibling($.first_child(fragment_2));
				var text_2 = $.child(span_2);

				$.reset(span_2);
				$.next();

				$.template_effect(() => $.set_text(text_2, `
            ${$.get(runningCount) ?? ''}
          `));

				$.append($$anchor, fragment_2);
			};

			$.if(node_1, ($$render) => {
				if ($.get(item).showCount && $.get(runningCount) > 0) $$render(consequent);
			});
		}

		$.next();
		$.reset(a);
		$.next();

		$.template_effect(
			($0) => {
				$.set_attribute(a, 'href', $.get(item).path);
				$.set_class(a, 1, `flex items-center gap-3 px-4 py-3 mx-2 rounded-lg transition-colors ${$0 ?? ''}`);
				$.set_text(text, $.get(item).icon);
				$.set_text(text_1, $.get(item).label);
			},
			[
				() => isActive($.get(item).path)
					? 'bg-slate-700 text-white'
					: 'text-slate-300 hover:bg-slate-800 hover:text-white'
			]
		);

		$.append($$anchor, fragment_1);
	});

	$.next();
	$.reset(nav);
	$.next(3);
	$.reset(aside);
	$.append($$anchor, fragment);
	$.pop();
}

if (import.meta.hot) {
	Sidebar = $.hmr(Sidebar, () => Sidebar[$.HMR].source);

	import.meta.hot.accept((module) => {
		module.default[$.HMR].source = Sidebar[$.HMR].source;
		$.set(Sidebar[$.HMR].source, module.default[$.HMR].original);
	});
}

export default Sidebar;

$.delegate(['click']);